.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu-toggle-svg {
  fill: #d41443;
}

.menu-toggle-svg-close {
  fill: #fff;
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: rgba(0, 0, 0, .1);
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #e17992;
  padding: 32px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

